@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
@import url("https://fonts.googleapis.com/css?family=Oswald&display=swap");

html {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0;
  font-family: Roboto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  flex: 1 0 auto;
  & p:last-child {
    margin-bottom: 2rem;
  }
  background: linear-gradient(
    138deg,
    rgb(26, 30, 36) 20%,
    rgb(40, 44, 52) 50%,
    rgb(26, 30, 36) 80%
  );
}

.recharts-responsive-container {
  margin: 3rem auto 1rem auto;
  transition: all 0.1s linear;
}
.recharts-default-tooltip {
  width: 140px;
  background: rgba(33, 37, 43, 0.95);
  text-align: left;
  padding: 0.9rem 1rem;
  box-shadow: 0.1em 0.1em 5em 0em rgba(0, 0, 0, 0.5);
  border: 1px solid #ccc;
  border-radius: 0.4rem;
  /* transform: perspective(300px) rotateY(-2deg); */
  & .recharts-tooltip-label {
    padding: 0;
    margin: 0 auto 0.4rem 0;
    text-align: center;
    font-weight: bold;
  }
  & .recharts-tooltip-item-list {
    margin: 0 auto;
    padding: 0 0.5rem;
    & .recharts-tooltip-item {
      list-style: none;
      margin-bottom: 0.15rem;
      & .recharts-tooltip-item-name {
        width: 50px;
      }
      & .recharts-tooltip-item-value {
        color: white;
        text-align: right;
      }
    }
  }
}

.recharts-surface {
  /* margin-left: -10%; */
  /* transform: perspective(300px) rotateY(-2deg); */
}
.recharts-legend-wrapper {
  /* transform: perspective(0px) rotateY(0deg); */
}
.recharts-legend-item-text,
.recharts-tooltip-item-name,
.recharts-surface path {
  user-select: none;
  font-family: Oswald;
  text-transform: capitalize;
  transition: all 0.2s linear;
}

.recharts-default-legend li {
  cursor: pointer;
}
.recharts-brush {
  & > rect:first-child {
    fill: rgb(26, 30, 36);
    fill-opacity: 1;
  }
  & .recharts-brush-slide {
    fill: rgb(40, 44, 52);
    fill-opacity: 1;
  }
}

.desactivated {
  display: none;
}

.recharts-line:not(.desactivated) {
  & path {
    transition: stroke-width 0.2s linear, stroke 0.2s linear;
  }
  &:has(+ .recharts-active-dot:hover) {
    & path {
      stroke-width: 6;
    }
  }
  &:hover {
    & path {
      stroke-width: 6;
    }
  }
}

.recharts-reference-line text {
  font-size: 0.7rem;
  fill: rgb(26, 30, 36);
  text-transform: none;
  font-style: italic;
  display: none;
  & tspan {
    fill: white;
  }
}

@media screen and (min-width: 600px) {
  .recharts-reference-line text {
    font-size: 0.7rem;
    display: block;
  }
  /* CSS styles for screens with a width greater than 800px */
}

@media screen and (min-width: 800px) {
  .recharts-reference-line text {
    font-size: 0.9rem;
    display: block;
  }
  /* CSS styles for screens with a width greater than 800px */
}

a {
  color: white;
  font-family: Oswald;
}

h1,
h2 {
  color: #eeeeee;
  font-family: Oswald;
  padding: 0 1rem;
  margin: 1em auto;
}
p {
  line-height: 1.4rem;
  margin: 1em auto;
  max-width: 720px;
  color: white;
  padding: 0em 1rem;
  text-align: left;
}
footer {
  background-color: rgb(26, 30, 36);
  border-top: 1px solid rgb(40, 44, 52);
  & div {
    padding: 1rem;
    max-width: 480px;
    margin: auto;
    margin-bottom: 1rem;
    color: gray;
  }
}
.info {
  display: inline-block;
  color: #dddddd;
  font-style: italic;
  margin-top: 1.5rem;
}
.reset {
  background: none;
  font-size: 0.9rem;
  color: white;
  font-family: Roboto;
  display: inline-block;
  font-style: normal;
  cursor: pointer;
  margin-left: 0.25rem;
  padding: 0.25rem 0.5rem;
  border-radius: 0.5rem;
  border: 3px solid rgb(102, 102, 102);
  transition: all 0.2s linear;
  &:hover {
    border: 3px solid white;
  }
}
